import {RadioGroup, RadioSize, RadioGroupChangePayload} from '@here/hds-radio';
import React, {useEffect, useImperativeHandle, useRef, useState} from 'react';

// eslint-disable-next-line no-new
new RadioGroup();

interface HDSRadioGroupProps extends Omit<React.HTMLAttributes<HTMLFormElement>, 'onChange'> {
  children: React.ReactNode;
  disabled?: boolean;
  vertical?: boolean;
  name?: string;
  value?: string;
  label?: string;
  size?: RadioSize;
  secondaryLabel?: string;
  onChange?: (event: CustomEvent<RadioGroupChangePayload>) => void;
}

const HDSRadioGroup = React.forwardRef(
  (
    {
      children,
      className,
      onChange,
      disabled,
      vertical,
      name,
      value,
      label,
      size,
      secondaryLabel,
      ...rest
    }: HDSRadioGroupProps,
    ref
  ) => {
    const [groupValue, setGroupValue] = useState(value);
    const innerRef = useRef<HTMLElement>(null);

    useImperativeHandle(ref, () => innerRef.current);

    const _handleSelected = (e: CustomEvent<RadioGroupChangePayload>) => {
      if (e.target !== innerRef.current) return;
      setGroupValue(e.detail.value);
      onChange && onChange(e);
    };

    useEffect(() => {
      if (innerRef.current) {
        innerRef.current.addEventListener('change', _handleSelected as EventListener);
      }
      return () => {
        innerRef.current?.removeEventListener('change', _handleSelected as EventListener);
      };
    }, [onChange, innerRef]);

    return (
      <hds-radio-group
        {...(disabled && {disabled: true})}
        {...(vertical && {vertical: true})}
        {...(label && {label})}
        {...(size && {size})}
        {...(secondaryLabel && {'secondary-label': secondaryLabel})}
        name={name}
        {...(className && {class: className})}
        value={groupValue}
        ref={innerRef}
        {...rest}
      >
        {children}
      </hds-radio-group>
    );
  }
);

HDSRadioGroup.defaultProps = {
  vertical: false,
  disabled: false,
  value: '',
  label: undefined,
  secondaryLabel: undefined,
  size: 'medium',
};

export default HDSRadioGroup;
